  .hero__title {
    font-family: 'Seymour One';
    font-style: normal;
    font-weight: 400;
    font-size: 26px;
    line-height: 1.27;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #FCFCFC;
    margin-left: 20px;
  }
  
  @media screen and (min-width: 768px) {
    .hero__title {
        margin-left: 32px;
    }
  }
  
  @media screen and (min-width: 1200px) {
    .hero__title {
      font-size: 38px;
