    font-size: 34px;
    line-height: 0.97;
  }
  
  @media screen and (min-width: 1200px) {
  .hero__big {
    font-size: 38px;
    line-height: 1.27;
  }
  }
  
  
  .hero__process {
    display: flex;
    align-items: center;
        
  }
  
  .hero__products {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    line-height: 1.25;
    color: #000000;
    border: 3px solid #F8D24E;
    border-radius: 10px;
    box-shadow: (0px 4px 4px rgba(0, 0, 0, 0.25));
    padding: 11px 26px;
    margin-right: 12px;
    margin-top: 32px;
    margin-left: 20px;
    margin-bottom: 236px;
    z-index: 2;
  }
  
  .hero__products:hover {
    border: 3px solid #0A8817;
    transition: border 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }

  .hero__products:focus {
    border: 3px solid #0A8817;
    color: #fcfcfc;
    transition: border 250ms cubic-bezier(0.4, 0, 0.2, 1),
    color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }

  .hero__products:disabled {
    border: 3px solid #ababab;
    transition: border 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }
  
  
  
  @media screen and (min-width: 480px) {
    .hero__products {
        margin-bottom: 150px;
    }
  }
  
  @media screen and (min-width: 768px) {
    .hero__products {
    padding: 12px 34px;
    margin-top: 24px;
    margin-left: 32px;
    margin-bottom: 178px;
    }
  }
  
  @media screen and (min-width: 1200px) {
    .hero__products {
    margin-top: 28px;
    margin-left: 16px;
    margin-bottom: 332px;
    }
  }
  
  
